'use client';

import { useCallback, useMemo } from 'react';
import { AuthContext } from './auth-context';
import { TUser } from 'src/constant/type';
import { useLocalStorage } from 'src/hooks/use-local-storage';
import { AUTH_USER } from 'src/constant/config';

// ----------------------------------------------------------------------

type AuthProviderProps = {
    children: React.ReactNode;
};

type TAuthStateType = {
    user: (TUser | null);
    authenticated: boolean;
    loading: boolean;
}

const initialState: TAuthStateType = {
    user: null,
    authenticated: false,
    loading: true,
};


const STORAGE_KEY = 'auth';

export function AuthProvider({ children }: AuthProviderProps) {


    const { state, update, reset } = useLocalStorage(STORAGE_KEY, initialState);

    const login = useCallback((user: TUser) => {
        if (user.email == AUTH_USER.email && user.password == AUTH_USER.password) {
            update("user", user)
            update("authenticated", true)
        }
    }, [])
    const memoizedValue = useMemo(
        () => ({
            user: state.auth,
            authenticated: state.authenticated,
            loading: state.loading === true,
            // 
            login,
            logout: reset,
        }),
        [
            reset, update, state
        ]
    );
    return <AuthContext.Provider value={memoizedValue}>{children}</AuthContext.Provider>;
}
