import { Suspense, lazy } from 'react';
import { RouteObject } from 'react-router';

import BaseLayout from 'src/layouts/base-layout';

import SuspenseLoader from 'src/components/suspense-loader';
import SignIn from './content/auth/sign-in';

const Loader = (Component) => (props) =>
(
  <Suspense fallback={<SuspenseLoader />}>
    <Component {...props} />
  </Suspense>
);

// transaction page

const Transactions = Loader(lazy(() => import('src/content/transactions')));

// 404 page

const Status404 = Loader(lazy(() => import('src/content/status')));

const routes: RouteObject[] = [
  {
    path: '',
    element: <BaseLayout />,
    children: [
      {
        path: '/',
        element: <SignIn />
      },
      {
        path: 'status',
        children: [
          {
            path: '404',
            element: <Status404 />
          }
        ]
      },
      {
        path: '*',
        element: <Status404 />
      }
    ]
  },

  {
    path: '/transaction',
    element: <BaseLayout />,
    children: [
      {
        path: '',
        element: <Transactions />
      }
    ]
  }
];

export default routes;
