'use client';

import { useContext, createContext } from 'react';
import { TUser } from 'src/constant/type';
// ----------------------------------------------------------------------



export type TAuthContextProps = {
    user: (TUser | null),
    loading: boolean,
    authenticated: boolean,
    logout: () => void,
    login: (updateUser: TUser) => void,
};

// ----------------------------------------------------------------------

export const AuthContext = createContext({} as TAuthContextProps);

export const useAuthContext = () => {
    const context = useContext(AuthContext);

    if (!context) throw new Error('useAuthContext must be use inside AuthProvider');

    return context;
};
