'use client';

import { useContext, createContext } from 'react';
import { EAlert } from 'src/constant/type';
// ----------------------------------------------------------------------


export type TSnackbarContextProps = {
    status: TSnackbarStateType,
    close: () => void,
    error: (msg: string) => void,
    warnning: (msg: string) => void,
    info: (msg: string) => void,
    success: (msg: string) => void,
};


export type TSnackbarStateType = {
    msg?: string;
    open: boolean;
    type?: EAlert;
}

export const SnackbarContext = createContext({} as TSnackbarContextProps);

export const useSnackbarContext = () => {
    const context = useContext(SnackbarContext);

    if (!context) throw new Error('useSnackbarContext must be use inside SnackbarProvider');

    return context;
};
