
export type TUser = {
    email: string
    password: string
}

export type TSnackbarStatus = {
    type: EAlert
    open: boolean
}


export enum EAlert {
    error = 'error',
    info = 'info',
    success = 'success',
    warning = 'warning'
}

export type ActionMapType<M extends { [index: string]: any }> = {
    [Key in keyof M]: M[Key] extends undefined
    ? {
        type: Key;
    }
    : {
        type: Key;
        payload: M[Key];
    };
};
