'use client';

import { useCallback, useMemo, useReducer } from 'react';
import { SnackbarContext, TSnackbarStateType } from './snackbar-context';
import { ActionMapType, EAlert } from 'src/constant/type';

// ----------------------------------------------------------------------

// ----------------------------------------------------------------------

enum Types {
    INITIAL = 'INITIAL',
    ERROR = 'ERROR',
    WANNING = 'WANNING',
    INFO = 'INFO',
    SUCCESS = 'SUCCESS',
}

type Payload = {
    [Types.INITIAL]: {
        msg?: string;
    };
    [Types.ERROR]: {
        msg: string;
    };
    [Types.WANNING]: {
        msg: string;
    };
    [Types.INFO]: {
        msg: string;
    };
    [Types.SUCCESS]: {
        msg: string;
    };
};

type Action = ActionMapType<Payload>[keyof ActionMapType<Payload>];

type SnackbarProviderProps = {
    children: React.ReactNode;
};

const initialState: TSnackbarStateType = {
    msg: null,
    open: false,
    type: null
};

const reducer = (state: TSnackbarStateType, action: Action) => {
    if (action.type === Types.INITIAL) {
        return {
            msg: "",
            open: false,
            type: EAlert.success
        };
    } if (action.type === Types.ERROR) {

        return {
            msg: action.payload.msg,
            open: true,
            type: EAlert.error
        }
    } if (action.type === Types.WANNING) {

        return {
            msg: action.payload.msg,
            open: true,
            type: EAlert.warning
        }
    } if (action.type === Types.INFO) {

        return {
            msg: action.payload.msg,
            open: true,
            type: EAlert.info
        }
    } if (action.type === Types.SUCCESS) {

        return {
            msg: action.payload.msg,
            open: true,
            type: EAlert.success
        }
    }
    return state;
};


export function SnackbarProvider({ children }: SnackbarProviderProps) {


    const [state, dispatch] = useReducer(reducer, initialState);

    const error = useCallback((msg: string) => {
        dispatch({
            type: Types.ERROR,
            payload: {
                msg: msg
            }
        })
    }, [])
    const warnning = useCallback((msg: string) => {
        dispatch({
            type: Types.WANNING,
            payload: {
                msg: msg
            }
        })
    }, [])
    const info = useCallback((msg: string) => {
        dispatch({
            type: Types.INFO,
            payload: {
                msg: msg
            }
        })
    }, [])
    const success = useCallback((msg: string) => {
        dispatch({
            type: Types.SUCCESS,
            payload: {
                msg: msg
            }
        })
    }, [])
    const close = useCallback(() => {
        dispatch({
            type: Types.INITIAL,
            payload: {
                msg: ""
            }
        })
    }, [])

    const memoizedValue = useMemo(
        () => ({
            status: state,
            // 
            error,
            warnning,
            info,
            success,
            close
        }),
        [
            state, error, warnning, info, success
        ]
    );
    return <SnackbarContext.Provider value={memoizedValue}>{children}</SnackbarContext.Provider>;
}
